.tile {
  align-items: center;
  background-color: white;
  border-radius: 4px;
  display: flex;
  height: 100px;
  justify-content: center;
  margin: 14px 0 0 14px;
  overflow: hidden;
  padding: 20px;
  width: 200px;
  cursor: pointer;

  .tile-icon {
    max-height: 100px;
    max-width: 200px;
  }
}
